    private emitEvent(type: keyof PluginEventListeners, pluginName: string): void {
        const listeners = this.eventListeners.get(type);
        if (listeners) {
            listeners.forEach(listener => {
                try {
                    listener(pluginName);
                } catch (error) {
                    this.config.logger?.error(`Error in ${type} listener: ${error}`);
                }
            });
        }
    }

    /**
     * 
     * @param type 
     * @param listener 
     */
    public addEventListener(type: keyof PluginEventListeners, listener: (pluginId: string) => void): void {
        if (!this.eventListeners.has(type)) {
            this.eventListeners.set(type, new Set());
        }
        this.eventListeners.get(type)!.add(listener);
    }

    /**
     * 
     * @param type 
     * @param listener 
     */
    public removeEventListener(type: keyof PluginEventListeners, listener: (pluginId: string) => void): void {
        const listeners = this.eventListeners.get(type);
        if (listeners) {
            listeners.delete(listener);
        }
    }
